import React, { useState, useEffect } from 'react';
import { fetchCharacters } from '../services';
import { useQuery, useDetails, useTotal, useOffset } from '../contexts';
import App from './App';

const getCharacters = async ({ setStatus, setCharacters, query, offset, total }) => {
  setStatus('loading');
  const characters = await fetchCharacters({ query, offset, total });

  if(characters) {
    setCharacters(characters);
    setStatus('ok');
  } else {
    setStatus('error');
  }
};

const useHooks = () => {
  const [status, setStatus] = useState(null);
  const [characters, setCharacters] = useState(null);

  const details = useDetails();
  const query = useQuery();
  const total = useTotal();
  const offset = useOffset();

  useEffect(() => {
    getCharacters({ setStatus, setCharacters, query, offset, total })
  }, [query.value, offset.value]);

  return {
    details,
    query,
    total,
    offset,
    status,
    characters
  };
};

const Enhanced = () => {
  const props = useHooks();
  return <App {...props} />
};

export default Enhanced;
