import React from 'react';
import Collection from '../Collection';
import {¬†categories } from '../constants';

import './References.scss';

const References = ({ current, setCurrent, details }) => (
  <div className="references">
    <nav className="tabs" data-current={current}>
      {categories.map((category, index) => (
        <button key={index} onClick={() => setCurrent(index)}>{category.label}</button>
      ))}
    </nav>
    <div className="tab-content" data-current={current}>
      {categories.map(category => <Collection key={details[category.key].collectionURI} URI={details[category.key].collectionURI} />)}
    </div>
  </div>
);

export default References;