import React, { useCallback, useContext } from 'react';
import Character from './Character';
import { Details } from '../contexts';

const getSrc = ({ thumbnail }) => `${thumbnail.path}/standard_medium.${thumbnail.extension}`;

const useHooks = ({ character }) => {
  const src = getSrc(character)  
  const details = useContext(Details);

  const displayDetails = useCallback(() => {
    details.set(character);
  }, [character, details])

  return {
    src,
    character,
    displayDetails
  };
};

const Enhanced = ({ character }) => {
  const props = useHooks({ character });
  return <Character {...props} />
};

export default Enhanced;
