import React from 'react';
import Loader from '../Loader';
import Error from '../Error';

const Collection = ({ status, collection }) => (
  <div className="tab-content__item">
    {{
      ok: (
        <>
          {collection && collection.length ? (
            <ul className="references-list">
              {collection.map(({ src, title }) => (
                <li key={title}>
                  <img src={src} alt={title}/>
                  {title}
                </li>
              ))}
            </ul>
          ) : (
            <p className="tab-content__message">Cette collection est vide.</p>
          )}
        </>
      ),
      loading: <Loader centered/>,
      error: <Error message="Impossible d'afficher les r√©f√©rences de ce personnage √† cause d'une erreur technique." />
    }[status]}
    
  </div>
);

export default Collection;