import React from 'react';
import Header from '../Header';
import Characters from '../Characters';
import Error from '../Error';
import Loader from '../Loader';
import CharacterDetails from '../CharacterDetails';
import { Query, Details, Total, Offset } from '../contexts';
import './App.scss';

const App = ({ status, characters, query, details, offset, total }) => (
  <Total.Provider value={total}>
    <Offset.Provider value={offset}>
      <Query.Provider value={query}>
        <Details.Provider value={details}>
          <Header />
          <main>
            {{
              loading: <Loader centered />,
              error: <Error message="Impossible de d'afficher la liste des personnages √† cause d'une erreur technique." />,
              ok: <Characters characters={characters} />
            }[status]}
            <CharacterDetails/>
          </main>
        </Details.Provider>
      </Query.Provider>
    </Offset.Provider>
  </Total.Provider>
);

export default App;